// echartsx
const addZero = (arg) => {
    if ((arg + "").length > 1) {
        return arg + ""
    } else {
        return "0" + arg
    }
}
export const getData = () => {
    var date = new Date();
    var year = date.getFullYear();
    var mouth = date.getMonth() + 1;
    var totalDays = new Date(year, mouth, 0).getDate();
    var arr = [];
    let mouthZero = addZero(mouth)
    for (let i = 1; i < totalDays + 1; i++) {
        let res = addZero(i)
        let result = `${mouthZero}-${res}`
        arr.push(result)
    }
    return arr
}